.App {
  /*text-align: center;*/
  /*display: */
  min-height: 100vh !important;
  width: 100vw;
}
.surahName{
  border-radius: 8px;
  margin:0 auto;
  margin-top:10px;
  text-align: center;
  padding: 25px 25px;
  width:85vw;
  color:rgba(0,0,0,0);
  background-color: #3CC47A;
  color: black;
  font-style: 'Quicksand', sans-serif;
  font-weight: bold;
  font-size: 20px;  
}
.menuBarContainer{
  padding: 15px;
  cursor: pointer;
  background-color: lightgrey;
  display: inline-block;
  border-radius: 20%;
  margin: 20px;
}
.menubar {
  width: 35px;
  height: 3px;
  border-radius:5px;
  background-color: black;
  margin: 6px 0;
}
.verse{

  /*box-shadow: 0 0 4px 2px lightgrey;*/
}
.loading{
  height: 50vh;
  width:100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}
.loader {
  border: 5px solid #f3f3f3; /* Light grey */
  border-top: 5px solid #3498db; /* Blue */
  border-radius: 50%;
  width: 60px;
  height: 60px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



