body {
  margin: 0;
  padding: 0;
  background-color: #1d1d2c;
  overflow-x: hidden;
  width: 100vw;
}


.App {
  /*text-align: center;*/
  /*display: */
  min-height: 100vh !important;
  width: 100vw;
}
.surahName{
  border-radius: 8px;
  margin:0 auto;
  margin-top:10px;
  text-align: center;
  padding: 25px 25px;
  width:85vw;
  color:rgba(0,0,0,0);
  background-color: #3CC47A;
  color: black;
  font-style: 'Quicksand', sans-serif;
  font-weight: bold;
  font-size: 20px;  
}
.menuBarContainer{
  padding: 15px;
  cursor: pointer;
  background-color: lightgrey;
  display: inline-block;
  border-radius: 20%;
  margin: 20px;
}
.menubar {
  width: 35px;
  height: 3px;
  border-radius:5px;
  background-color: black;
  margin: 6px 0;
}
.verse{

  /*box-shadow: 0 0 4px 2px lightgrey;*/
}
.loading{
  height: 50vh;
  width:100vw;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  overflow: hidden;
}
.loader {
  border: 5px solid #f3f3f3; /* Light grey */
  border-top: 5px solid #3498db; /* Blue */
  border-radius: 50%;
  width: 60px;
  height: 60px;
  -webkit-animation: spin 2s linear infinite;
          animation: spin 2s linear infinite;
}

@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}




.sidenav {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 20;
  top: 0;
  left: 0;
  background-color: #111;
  overflow-x: hidden;
  transition: 0.5s;
  padding-top: 60px;
}

.sidenav-button {
  padding: 8px 8px 38px 32px;
  text-decoration: none;
  font-size: 18px;
  color: #818181;
  display: block;
  transition: 0.3s;
  cursor: pointer;
}

.sidenav-button:hover {
  color: #f1f1f1;
}

.sidenav .closebtn {
  position: absolute;
  top: 0;
  right: 25px;
  font-size: 36px;
  margin-left: 50px;
}

@media screen and (max-width: 10in) {
  .sidenav {padding-top: 15px;}
  .sidenav-button {font-size: 18px;}
}
.verseCard{
	border-radius: 8px;
	margin:0 auto;
	margin-top:10px;
	text-align: left;
	padding: 25px 25px;
	width:50vw;
	color:rgba(0,0,0,0.7);
	background-color: #353849;
	color: lightgrey;
	font-style: 'Quicksand', sans-serif;
	font-weight: normal !important;
	font-size: 15px !important;
}
.verseNumber{
	top: -20px;
	left: -20px;
	position: relative;
	font-size: 10px;
}
@media only screen and (max-width: 10in ){
  .verseCard{
    width: 85vw;
  }
}
